.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 40px;
  margin: 0 auto;
  padding: 0 20px;
  width: 1200px;
}
.keyboard {
  display: flex;
  flex-wrap: wrap;
  gap:10px;
  width: 1100px;
  height: 400px;
  padding: 10px;
  background-color: antiquewhite;
  border: 2px solid black; 
}
.textarea{
  width: 70%;
  min-height: 300px;
}
.key{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 6%;
  height: 15%;
  color:antiquewhite;
  font-size: 20px;
  font-weight: 700;
  background-color: black;
  border-radius: 10px;
  box-shadow: 0 0 10px black;
  transition: all 0.3s;
}

.key--big{
  width: 10%;
}
.key--extra-big{
  width: 37.7%;
}

.key--active{
  background-color: black;
  transform: scale(0.85 );
}

.capslock::after{
  position: absolute;
  content: '';
  right: 7px;
  top: 7px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: rgba(255,255,255, 0.3);
}
.capslock--active::after{
  background-color: rgb(212, 40, 40);
}